.review-date {
  font-size: em(14px);
  color: $clr15;
  // align-self:start;
}

.reviewer {
  // display: flex;
  align-items: center;
  // padding: 20px 0;
  // border-bottom: 1px solid $clr15;
  &:last-child {
    border-bottom: none;
  }
}

.review-msg {
  justify-content: start;
  line-height: 1.5;
}

.reviews-container {
  & .nickname-review {
    & .reviewer-name {
      align-self: flex-start;
      // color: $clr4;
    }
    justify-content: flex-start;
    gap: 20px;
  }
  .review-preview-details {
    display: flex;
  }
  & .reviewer {
    margin-bottom: 20px;
  }
  .reviewer-location-country {
    color: $clr4;
  }
  .reviewer-img img {
    height: 48px;
    width: 48px;
    object-fit: cover;
    align-self: baseline;
  }
  .nickname-review {
    display: flex;
    gap: 0;
    gap: 10px;
    // flex-direction: row;
  }

  .reviewer-item {
    margin-right: 15px;
  }

  .review-date {
    padding-top: 10px;
  }

  .review-msg {
    color: $clr1;
  }
}

.reviews-carusel {
  & .carousel .slide {
    text-align: start;
    & .reviewer-location img {
      height: 30px;
      width: 30px;
      object-fit: cover;
    }
    & .review-item {
      gap: 5px;
    }

    & .review-date {
      //   line-height: 21px;
      margin-top: 7px;
    }

    & .reviewer-img.reviewer-item img {
      height: 32px;
      width: 32px;
      object-fit: cover;
      align-self: baseline;
      // padding-top: 5px;
    }
  }
}

.reviewer-item {
  gap: 5px;
  align-items: center;
  margin-right: 15px;
}

.reviewer-img {
  img {
    border-radius: 50%;
    height: 30px;
    width: 30px;
    object-fit: cover;
    // margin-right: 16px;
  }
}

.seller-overview {
  line-height: 24px;
}
.reviewer-name {
  align-self: center;
  font-weight: 700;
  // color: $clr4;
}

.reviewer-location {
  font-size: em(14px);
  color: $clr4;
  img {
    width: 20px;
    margin-right: 4px;
  }
}

.reviewer-rate {
  svg {
    width: 20px;
    color: $clr6;
  }
}
