.gig-preview {
    width: em(326px);
    background-color: $clr-1;
    border: 1px solid $clr14;
    // margin-bottom:34px;
    color: $clr11;
    flex-direction: column;
    // height: em(398px);
    // height: 398px;
    &:hover {
        p {
            color: $clr2;
        }
    }

    .preview-main-container {
        flex: 1;
    }

    .top-gig-section {
        display: flex;
        flex-direction: column;
        flex: 1;
        a {
            color: $clr11;
            display: flex;
            flex-direction: column;
            flex: 1;
        }
    }

    .middle-gig-section {
        flex: 1;
        p {
            flex: 1;
        }
    }
    .preview-img-container{
        height: em(195px);
        // width: em(324px);
        .preview-img {
            object-fit: cover;
            height: 100%;
            width: 100%;
            margin: 0;
        }
    }
    

    h4 {
        display: inline-block;
        font-size: em(14px);
        line-height: 1.285;
        font-family: Macan-Bold;
        margin: 22px 0 3px 0;
    }

    .owner-icon-container {
        width: 24px;
        height: 24px;
        margin: 17px 8px 0 12px;
    }
    .owner-icon {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
    }

    .owner-info {
        display: flex;
    }

    p {
        word-wrap: break-word;
        padding: 0 13px 0 12px;
        margin-bottom: 12px;
        line-height: 1.3;
    }

    .rate {
        font-size: em(14px);
        color: $clr6;
        font-weight: 700;
        margin-right: 3px;
    }

    .gig-preview-footer {
        display: flex;
        padding: 0;
        align-items: center;

        .menu-svg {
            margin: 0 5px 0 12px;
        }
    }

    hr {
        width: 100%;
        border: none;
        border-top: 1px solid $clr14;
        margin: 0;
    }

    .star-box {
        margin: 0 5px 8px 10px;
    }

    .rate {
        font-size: em(13px);
        padding-top: 3px;
    }

    .total-likes {
        font-size: em(14px);
        color: $clr13;
        padding-top: 3px;
    }

    .price-container {
        // flex-direction: column;
        padding: 10px 11px 0 0;
        align-items: center;
        
    
    }

    .price-preview {
        color: $clr5;
        line-height: 1.25;
        font-family: Macan-bold;
        font-weight: 700;
        font-size: em(11px);
        // letter-spacing: 0.1px;
        margin: 0 8px 8px 0;
    }

    .price-tag {
        line-height: 1.25;
        font-size: em(18px);
        align-self: flex-end;
        margin-bottom: 11px;
    }

    .sup-price-end {
        font-size: 10px;
    }

    .preview-footer-fav {
        margin-left: 10px;
        cursor: pointer;
       
    }
}

.gig-preview-star {
    fill: $clr6;
    // margin: 0 1px 11px 12px;
}
.gig-preview-opt-clr {
    fill: $clr13;
    &.clicked{
        fill:$clr18;
    }
    
}

.star-svg,
.fav-svg {
    padding: 0;
    display: inline-block;
    margin: 0;
}

.order-list-status {
    font-size: em(12px);
}

@media (max-width: 1300px) {
    .gig-preview {
        width: 100%;
    }
}
